import React from 'react';
import { ScoreCardTable } from '../ScoreCardTable/ScoreCardTable';
import { useEntity } from '@backstage/plugin-catalog-react';

type ScoreCardTableProps = {
  title?: string;
  entityKindFilter?: string[];
};

/**
 * EntityScoreCardTable is a wrapper around ScoreCardTable
 * that automatically fetches the entity from the catalog and
 * passes it to ScoreCardTable so that the entity can be used to
 * load information about the location of the scores
 **/
export const EntityScoreCardTable = ({
  title,
  entityKindFilter,
}: ScoreCardTableProps) => {
  const { entity } = useEntity();

  return (
    <ScoreCardTable
      title={title}
      entityKindFilter={entityKindFilter}
      entity={entity}
    />
  );
};
